.backTo2 {
  position: absolute;
  height: 50px;
  width: 50px;
  background: url("../../image/twitch.0");
  background-size: 50px 50px;
}

.nextTo4 {
  position: absolute;
  height: 50px;
  width: 50px;
  background: url("../../image/twitch.0");
  background-size: 50px 50px;
}

.wall {
  position: absolute;
  height: 50px;
  width: 50px;
  background-color: #845332;
}

@keyframes RBgan {
  0% {
    background: url("../../image/rb_gan.0");
    background-size: 50px 50px;
  }
  50% {
    background: url("../../image/rb_gan2.0");
    background-size: 50px 50px;
  }
  100% {
    background: url("../../image/rb_gan.0");
    background-size: 50px 50px;
  }
}

@keyframes Samgan {
  0% {
    background-image: url("../../image/sam_gan.0");
    background-size: 50px 50px;
  }
  50% {
    background-image: url("../../image/sam_gan2.0");
    background-size: 50px 50px;
  }
  100% {
    background-image: url("../../image/sam_gan.0");
    background-size: 50px 50px;
  }
}

@keyframes ARgan {
  0% {
    background-image: url("../../image/ar_gan2.0");
    background-size: 50px 50px;
  }
  50% {
    background-image: url("../../image/ar_gan.0");
    background-size: 50px 50px;
  }
  100% {
    background-image: url("../../image/ar_gan2.0");
    background-size: 50px 50px;
  }
}

@keyframes SMORC {
  0% {
    transform: rotateZ(0);
    background-size: 50px 50px;
  }
  25% {
    transform: rotateZ(5deg);
    background-size: 50px 50px;
  }
  50% {
    transform: rotateZ(0);
    background-size: 50px 50px;
  }
  75% {
    transform: rotateZ(5deg);
    background-size: 50px 50px;
  }
  100% {
    transform: rotateZ(0);
    background-size: 50px 50px;
  }
}
